.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.App-error {
  margin: 5px;
  padding: 5px 2px;
  background-color: #F90000;
  color: #440000; 
  border: .1rem solid #910000;
  border-radius: 10px;
}
.App-result {
  margin: 5px;
  padding: 5px 2px;
  background-color: #00F900;
  color: #004400; 
  border: .1rem solid #009100;
  border-radius: 10px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
    filter: invert(10%);
  }
  to {
    transform: rotate(360deg);
    filter: invert(50%);
  }
}
